<template>
  <div>
    <div id="search_header">
      <div class="btn-group" role="group" aria-label="...">
        <button type="button" class="btn btn-default" @click="showallcars"></button>
      </div>
      <input type="text" placeholder="" v-model="search">
      <div class="btn-group" role="group" aria-label="...">
        <button type="button" class="btn btn-default" @click="searchcar(1)"></button>
        <button type="button" class="btn btn-default" @click="searchcar(2)"></button>
        <button type="button" class="btn btn-default" @click="searchcar(3)"></button>
      </div>
    </div>
    <label for="result">{{length}} </label>
      <table class="table table-hover">
        <thead>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(order, index) in orders" :key="index">
            <td>{{order.id}}</td>
            <td>{{order.type}}</td>
            <td>{{order.good}}</td>
            <td>{{order.goodNum}}</td>
            <td>{{order.price}}</td>
            <td>{{order.state}}</td>
            <td>
              <button v-show="order.state==''" class="btn btn-success btn-sm" @click="confim(order)"></button>
              <button v-show="order.state==''" class="btn btn-danger btn-sm" @click="del(order, index)"></button>
            </td>
          </tr>
        </tbody>    
      </table>
      <nav aria-label="Page navigation">
        <ul class="pagination">
            <li>
              <a aria-label="Previous" @click="prevpage">
              <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li v-for="(val,index) in indexstr" :key="index"><a @click="indexpage(index)">{{index+1}}</a></li>
            <li>
              <a aria-label="Next" @click="nextpage">
              <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
        </ul>
      </nav>
  </div>
</template>

<script>
import {reqDelOrder,reqConfirmOrder} from '../../api'
export default {
  data () {
    return {
      allorders: [],
      searchorders: [],
      orders:[],
      pageindex: 0,
      length: 0,
      indexstr: [],
      search: '',
    }
  },
  async mounted () {
    await this.$store.dispatch('getAllOrders')
    this.allorders = this.$store.state.orders
    this.allorders.forEach(item=>item.state==1?item.state='':item.state='')
    this.length = this.allorders.length
    this.searchorders = this.allorders
    this.orders = this.searchorders.slice(0,10)
    for(let i = 0; i<this.length/10; i++){
      this.indexstr[i] = i;
    }
  },
  methods: {
    nextpage(){
      this.pageindex++
      if(this.pageindex>=this.length/10)this.pageindex--
      this.orders = this.searchorders.slice(this.pageindex*10,(this.pageindex+1)*10)
    },
    prevpage(){
      this.pageindex--
      if(this.pageindex<0)this.pageindex++
      this.orders = this.searchorders.slice(this.pageindex*10,(this.pageindex+1)*10)
    },
    indexpage(index){
      this.pageindex = index
      this.orders = this.searchorders.slice(this.pageindex*10,(this.pageindex+1)*10)
    },
    showallcars(){
      this.search = ''
      this.searchorders = this.allorders
      this.length = this.searchorders.length
      this.orders = this.searchorders.slice(0,10)
      this.indexstr = []
      for(let i = 0; i<this.length/10; i++){
        this.indexstr[i] = i;
      }
    },
    searchcar(type=1,text=this.search){
      if(type==1)this.searchorders = this.allorders.filter((item)=>item.id == text)
      if(type==2)this.searchorders = this.allorders.filter((item)=>item.state == '')
      if(type==3)this.searchorders = this.allorders.filter((item)=>item.state == '')
      this.length = this.searchorders.length
      this.orders = this.searchorders.slice(0,10)
      this.indexstr = []
      for(let i = 0; i<this.length/10; i++){
        this.indexstr[i] = i;
      }
    },
    async confim(o){
      let r = confirm(""+o.id+"")
      if(r==false)return
      const data = 
        {
          id: o.id
        }
      const result = await reqConfirmOrder(data)
      if(result.code==1)
      {
        alert(result.message)
        o.state = ''
      }
      else alert(result.message)
    },
    async del(o,index){
      let r = confirm(" "+o.id+" ")
      if(r==false)return
      const data = 
        {
          id: o.id
        }
      const result = await reqDelOrder(data)
      if(result.code==1)
      {
        alert('')
        o.state = ''
      }
      else alert('')
    }
  }
}
</script>

<style>

</style>


